.dist2{
  width: 100%;
  section:nth-child(2n + 0) {
    background: #FAFAFA;
  }
  section:nth-child(2n + 1) {
    background: #FFF;
  }
  section{
    position: relative;
    display: inline-block;
    width: 100%;

    .section-content-item{

      display: inline-block;
      position: relative;
      height: 336px;
      width: 50%;
    }
    .section-content-item-pic{
      float: left;
      .md-img {
        position: absolute;
        margin: auto;
        top: 0;
        bottom: 0;
      }
    }
    .section-content-item-text{
      float: right;
      text-align: left;
      display: table;
    }

    &.reverse{
      .dist-content {
        .section-content-item-pic{
          float: right;

          .md-img {
            right: 0;
            left: 0;
          }
        }
      }

      .section-content-item-text{
        float: left;
        text-align: right;
      }

    }
    @include iphone6 {
      height: 80%;
      .section-content-item{
        float: none;
        width: 100%;
      }
      .section-content-item-text {
        ul{
          width: 100%;
        }
        p{
          text-align: center;
        }

      }
    }

  }
  .text-content{

    max-height:400px;
    text-align:left;
    @include iphone6{
      padding-left: 0;
    }
  }
  margin-top:　10px;
  height: auto;
  .md-text-filed{
    .caption-head{
      font-size: 2rem;
      color:#13A277;
    }
  }
  .caption-list{
    position: relative;
    text-align: left;
    list-style: none;
    margin: 0;
    padding: 0;
    display: inline-block;

    .caption-list-item{
      margin-top:40px;
    }
    .caption-text{
      @extend .md-body-regular-light;
    }
  }
}

@import './dist2-media';
