.dist5{
    height:auto;
    width:100%;
    .dist5-title-container{
        margin-top:150px;
        text-align: center;
        color:black;
        .dist5-title-main{
            margin-bottom:20px;
        }
    }
    .client-box-container{
        .client-box{
            width: 95%;
            margin:16px auto;
            .client-item{
                position:relative;
                @include size(137px);
                margin: 0 11px 11px 0;
                display:inline-block;

                @include border-radius(2px);
                background-color: #f4f4f5;
                background-position: center;
                background-repeat: no-repeat;
                .bg{
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    border-radius: 2px;
                    background-color: #40C66C;
                    @include opacity(0);
                    @include animation('moveOut 0.4s ease');
                }
                &:hover .bg{
                    @include animation('moveIn 0.2s ease');
                    @include opacity(1);
                }
                .client-logo{
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background-image:url("/static/img/clients.png");
                }
            }
            $logos : (
                ('gq',-274px,0px,-274px,-548px),
                ('zhihu',-548px,-137px,-685px -685px),
                ('meizu',-137px,-274px,-685px,0px),
                ('catchyou',0px,0px,-548px,-274px),
                ('dgtle',0px,-137px,0px,-548px),
                ('fengguo',-137px,-137px,-137px,-548px),
                ('knewcoin',-274px,-137px,-411px,-548px),
                ('litb',0px,-274px,-548px,-548px));

            @each $logo_name,$bx,$by,$ax,$ay in $logos {
                .#{$logo_name}{
                    background-position: $bx $by;
                }
                .#{$logo_name}:hover{
                    background-position: $ax $ay;
                }
            }
        }
    }
}
@include keyframes(moveIn){
    0%{
        @include prefix(transform,scale(0.8));
        @include opacity(0);
    }
    100%{
        @include prefix(transform,scale(1));
        @include opacity(0.95);
    }
}
@include keyframes(moveOut){
    0%{
        @include prefix(transform,scale(1));
        @include opacity(0.95);
    }
    100%{
        @include prefix(transform,scale(0.8));
        @include opacity(0);
    }
}

@import './dist5-media';
