.homepage-container {
  position: relative;

  .dist-content {
    height: 100%;
    max-width: 960px;
    margin: 0 auto;
    padding: 0 72px;
    font-size: 0;

    @include max-screen(768px) {
      padding: $header-height 24px 0 24px;
    }

    .section-title {
      font-size: 20px;
      color: #14A176;
      text-align: center;
      padding-top: 24px;
      font-weight: bold;
    }

    .section-content {
      margin-top: 48px;
    }

    .section-content-item {

      .section-content-item-pic, .text-content {
        display: table-cell;
        vertical-align: middle;
      }
    }
  }

  footer {
    display: block;
  }
}

div[class^="dist"] {
  position: relative;
  width:100%;
}

.register,
.forgot,
.reset,
.login,
.claim,
.invitationcode-apply,
.opt-in{
  position:relative;
  width:100%;
  height:100%;

  &.mobile {
    display: none;
  }
}

[data-pjax] {
  width: 100%;
  height: 100%;
}

.invitationcode-apply {

  .card{

    .name{
      margin-right:8%;
    }

    .apply-card {
      padding: 24px;
    }
  }
}
.claim {

  .card-list {
    width:100%;
  }

  .card-content {
    margin:0;
  }

  .basic-info {
    margin-bottom: 0;

    .input-field {
      margin-right: 16px;
    }
  }
  .agreements {

    label {
      padding-left: 28px;
    }

    a {
      color: lighten($collection-text-color,16%);
    }
  }

  .files-upload{
    width:100%;
    height:122px;
    label.active{
      @extend .md-body-regular;
      color:rgba(0,0,0,0.87) !important;
      margin-top:24px;
    }
  }
  .file-btn{
    position:relative;
    width:328px;
    height:73px;
    top:16px;
    background: url('../images/icon-+.png') no-repeat;
    background-position:50% 38%;
    span{
      display:inline-block;
      position:absolute;
      text-align:center;
      margin:auto;
      left:0;right:0;
      line-height:73px;
      top:12%;
    }
  }
}

@media (max-width: 30em) {
  .row{
    .col{
      width:100% !important;
    }
  }
  .dist1{
    .left{
      text-align:center;
    }
  }
}
.slideDownOut{
  @include animation('slideDownOut 1s ease forwards');
}
.slideDownIn{
  @include animation('slideDownIn 0.8s ease forwards');
}
@include keyframes(slideDownOut){
  0% {
    @include opacity(1);
    @include prefix(transform,translate3d(0,0,0));
  }
  100% {
    @include opacity(0);
    @include prefix(transform,translate3d(0,90%,0));
  }
}
@include keyframes(slideDownIn){
  0% {
    visibility:hidden;
    @include opacity(0);
    @include prefix(transform,translate3d(0,-150%,0));
  }
  100% {
    visibility:visible;
    @include opacity(1);
    @include prefix(transform,translate3d(0,0%,0));
  }
}
.animated {
  animation-duration: 1s;
  animation-fill-mode: both;
}

.content{
  position: absolute;
  width: 960px;
  height: 448px;
  margin: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-image: url('/static/images/slash/bg.png');
}
.show-main,
.show-intro{
  height: 100%;
  display: inline-block;
  vertical-align: top;
}

.click-home{
  width: 250px;
  height: 100px;
  position: relative;
  .btn{
    position: absolute;
    margin: auto;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: inline-block;
    border: none;
    border-radius: 2px;
    height: 36px;
    width:150px;
    line-height: 36px;
    padding: 0 2rem;

    outline: 0;
    font-size: 14px;
    text-transform: uppercase;
    text-decoration:none;
    text-align: center;
    letter-spacing: .5px;
    vertical-align: middle;
    // Gets rid of tap active state
    -webkit-tap-highlight-color: transparent;
    cursor:pointer;

    background: #20C05C;
    *{
      color: white;
    }
  }
}

@keyframes bounceInLeft {
  0%, 60%, 75%, 90%, 100% {
    animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
  }

  0% {
    opacity: 0;
    transform: translate3d(-3000px, 0, 0);
  }

  60% {
    opacity: 1;
    transform: translate3d(25px, 0, 0);
  }

  75% {
    transform: translate3d(-10px, 0, 0);
  }

  90% {
    transform: translate3d(5px, 0, 0);
  }

  100% {
    transform: none;
  }
}

.bounceInLeft {
  animation-name: bounceInLeft;
}
@keyframes bounceInUp {
  0%, 60%, 75%, 90%, 100% {
    animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
  }

  0% {
    opacity: 0;
    transform: translate3d(0, 3000px, 0);
  }

  60% {
    opacity: 1;
    transform: translate3d(0, -20px, 0);
  }

  75% {
    transform: translate3d(0, 10px, 0);
  }

  90% {
    transform: translate3d(0, -5px, 0);
  }

  100% {
    transform: translate3d(0, 0, 0);
  }
}

.bounceInUp {
  animation-name: bounceInUp;
}


/* 用户相应模块的样式 */
/* 拒绝卡片 */
.refuse-card {
  .card-text-content {

    p.card-caption {
      font-size: 14px;
    }
  }
}
.rejection-list {

  .rejection-item {

    cursor: pointer;
    padding: 0 8px;
    height: $list-height;
    line-height: $list-height;
    @extend .rejection-item-font;
    &:hover, &.active {
      background: #FAFAFA;
      i {
        display: block;
      }
    }
    i {
      display: none;
      float: right;
      height: $icon-height;
      margin-top: ($list-height - $icon-height)/2;
    }

  }
}
#intention-form {

  .form-content {
    margin-top: 32px;
  }
}

.member-login,
.member-resetpwd,
.member-forgotpwd{
  .card-field-content {
    padding: 0;
  }


}
form#login-form {

  .input-field-content {
    padding: 8px 16px;
  }
}

.login-type {

  font-size: 0;

  .login-type-item {
    @extend .member-login-font;

    display: inline-block;
    width: 50%;
    height: 48px;
    line-height: 48px;
    text-align: center;
    cursor: pointer;
    padding: 0 12px;
    position: relative;

    .login.indicator {
      display: none;
      height: 2px;
      position: absolute;
      left: 64px;
      width: 56px;
      bottom: 8px;
    }
    &.active{
      span {
        color: $primary-color;
      }
      .login.indicator {
        border-bottom: 2px solid $primary-color;
        display: block;
      }

    }
    &:hover{

      .login.indicator {
        border-bottom: 2px solid $primary-color;
        display: block;
      }
    }
  }
}

/* 提示消息 */
.login-regist-info {
  display: none;
  text-align: center;
  padding: 72px 0 24px 0;
  margin: 0 auto;
  color: rgba(0,0,0,0.54);
  p {
    padding-top: 24px;
  }
}
/* 以下是自适应部分其实是适配部分 */
.main-container {
  @include max-screen(768px) {
    width: 100%;
  }
}

.mobile-form {

  .input-field {

    @include max-screen(480px) {
      margin-top: 0;

      label {
        display: none;
      }
      input {
        height: 48px;
        font-size: 1.6rem;
      }
      .select-wrapper {

        input.select-dropdown{
          height: 48px !important;
          font-size: 16px;
        }

        .dropdown-content li > a, .dropdown-content li > span {
          font-size: 1.6rem;
        }
      }
    }
  }
  @include max-screen(480px) {
    .card-action {
      padding: 0 24px;
      .confirm {
        width: 100%;
        float: none !important;
        height: 48px;
        line-height: 48px;
        font-size: 16px;
      }
      .cancel {
        display: none;
      }
    }
  }

}

.register {
  .register-logo {
    display: none;
  }
  @include max-screen(480px) {
    .card-header.pc {
      display: none;
    }
    .register-logo {
      display: block;
      text-align: center;
    }

    .card {
      margin-top: 18px;
      padding: 0 0 24px 0 ;

      .input-field {
        margin-top: 0;
      }
    }
    .card-action {
      padding: 0;
      .btn {
        width: 100%;
        font-size: 16px;
      }
    }
  }
}

.invitationcode-apply {
  @include max-screen(480px) {
    .card {
      margin: 0;
      width: 100%;
      max-width: 100%;
    }
    .card-content-wrapper {
      height: 91px;
      background: rgba(58, 62, 66, 1);
    }
  }
}

.ask-for {
  font-size: 0;
  padding-top: 56px;

  .head-photo {
    display: inline-block;
    vertical-align:top;
    width: 60%;
  }
  .head-content {
    display: inline-block;
    width: 40%;
    padding-top: 24px;

    .invite-code {
      font-size: 0;
      * {
        display: inline-block;
        margin: 0;
        vertical-align: top;
      }

      #invitation-code {
        width: 240px;
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
      }

      #invitation-confirm {
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
      }
    }
  }
}

footer {
  display: block;
}
