.dist2 {

  .head-action.mobile {
    display: none;
    margin-top: 0;

    .btn.action {
      width: 128px;
      height: 44px;
      line-height: 44px;
      position: absolute;
      bottom: 56px;
      display: inline-block;
      left: 0;
      right: 0;
      margin: auto;
    }

    @include max-screen(480px) {
      display: block;
    }
  }
  @include max-screen(480px) {
    height: 80%;

    .dist-content {

      .section-content-item{
        float: none;
        width: 100%;
        height: 50%;
      }
      .section-content-item-pic {
        .md-img {
          @extend .center;
        }
      }

    }
    .section-content-item-text {

      .text-content {
        padding: 0 36px !important;
      }
      ul{
        width: 100%;
      }
      p{
        text-align: center;
      }
    }
  }
}
