.faq {
  @include max-screen(768px) {
    .dist-content {
      background: #FFF;

      .section-content-item{
        float: none;
        width: 100%;
      }
    }

    .faq-title {
      text-align: center !important;

      p {
        text-align: center;
      }
    }
    /* .section-content-item {
       height: 40%;
       }
     */
    .section-content-item.answer {
      padding: 0;
      background: #FFF;
      .answer-content {
        ul {
          border: none;
        }
      }
    }

    .section-content-item-text {
      background: #EAEAEA;
      padding: 24px;

      .caption-text  {
        text-align: left;
      }
    }
  }

}
