$events-padding: 12px;
$events-width: 720px;
$events-height: 240px;
$events-image-ratio: $events-height / $events-width;


.events-collection {
  position: relative;

  .section-content {
    background: #FFF;
    bottom: 0;
    margin: 0 auto;
    padding: $events-padding;
    padding-bottom: 0;
    position: relative;
    width: 100%;
    height: $events-height + $events-padding ;
    max-width: $events-width + 2 * $events-padding ;
  }
  .events-lists-content {
    height: 100%;
  }
  .events-lists {
    background: #E6E6E6;
    width: 100%;
    height: 100%;
    position: relative;

    .event-item {
      /* display: block !important; */
      height: 100%;
      position: relative;
      width: 100%;

      img {
        max-width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
      }
    }
  }
}

.events-lists-content .dots {
  position: absolute;
  text-align: center;
  left: 0;
  bottom: 6px;
  width: 100%;
}
.events-lists-content .dots li {
  background: rgba(255,255,255,0.54);
  display: inline-block;
  *display: inline;
  zoom: 1;

  @include size(6px);
  line-height: 6px;
  margin: 0 4px;

  text-indent: -999em;
  *text-indent: 0;

  border-radius: 6px;
  cursor: pointer;
  opacity: .4;

  -webkit-transition: background .5s, opacity .5s;
  -moz-transition: background .5s, opacity .5s;
  transition: background .5s, opacity .5s;
}
.events-lists-content .dots li.active {
  background: #fff;
  opacity: 1;
}

@import './dist3-media';
