.dist1 {

    @include max-screen($phone-screen) {
        .dist-content {
            display: table-cell;
            vertical-align: middle;

            .dist-content-main {
                height: 100%;
            }

            .head-content{
                width: 100%;
                text-align: center;
                height: 50%;

                .head-action {
                    margin-top: 18px;

                    .btn-flat{
                        margin-top: 18px;
                        height: 44px;
                        line-height: 44px;
                        width: 128px;
                    }

                }
            }

            .head-top {
                margin-top: 24px;
            }

            .head-caption {
                margin-top: 18px;
            }

            .intro-media {
                height: 50%;
                width: 100%;
                margin: 0;

                img {
                    max-width: 100%;
                    max-height: 100%;
                }
            }
        }
        
        .mobile-tmp-login {
            font-size: 14px;
            width: 100%;
            display: inline-block;
            color: rgba(255,255,255,0.87);
            margin-top: 12px;
            text-decoration: underline;
            cursor: pointer;
        }
    }
}
