$dist1-height:  500px;

.dist1{
    background-color: #20C05C;
    @include bg-gradient-t2b(#20C05C, #13A277);
    /* background-image: url("/static/img/bg-v2.png"); */
    background-repeat: no-repeat;
    /* IE9以上 */
    background-size: cover;
    background-position: 50% 50%;
    height: 100%;
    min-height: $dist1-height;
    padding-top: $header-height;
}

.intro-media {
    display: inline-block;
    vertical-align: top;
    height: $dist1-height - $header-height;
    width: 35%;
    font-size: 36px;
    position: relative;

    .media-content {
        background: transparent;

        img {
            position: absolute;
            left: 0;
            bottom: 0;
        }
    }

}
.dist-content {
    .head-content{
        display: inline-block;
        width:65%;
        height: $dist1-height - $header-height;
        text-align: left;
        display:inline-block;

        .head-caption{
            font-size: 1.6rem;
            color: #FFF;
        }
    }
    .head-photo{
        float: right;
        position:relative;
        display:inline-block;
        width:60% ;
        height: 100%;

        img {
            @extend .center;
            max-width: 100%;
            max-height: 100%;
        }
        @include iphone6{
            float: none;
            width: 100%;
            height: 40%;
            img{
                margin-bottom: 0;
            }
        }
    }
    .head-top {
        margin-top: 120px;
    }
    .head-title{
        margin-top: 12px;
        width: 100%;

        .mobile {
            display: none;
        }

        @include max-screen($phone-screen) {
            .pc {
                display: none;
            }

            .mobile {
                display: block;
            }
        }
        p {
            font-weight: bold;
            font-size: 2.4rem;
            color: #FFF;
            letter-spacing: 2px;
            margin: 0;
        }
    }
    .head-caption{
        height:63px;
        margin-top: 40px;
    }
    .head-action {

        .btn-flat {
            margin: 0;
            &:hover {
                background: #EEEEEE;
                color: #13A277;
            }
        }
        margin-top: 60px;
    }
}

@import './dist1-media';
