/* 常见问题部分 */
.faq {
  background: #FFF;
  .faq-title {
    width: 100%;
    line-height: 84px;
    display: inline-block;
    font-size: 20px;
    text-align: right;
    color: #14A176;
    margin: 0;
    padding: 0;
    font-weight: bold;
    p {
      display: inline-block;
      width: 50%;
      text-align: left;
    }
  }

  .section-content-item {
    width: 50%;
    font-size: 0;
    position: relative;
    padding: 32px 0;
    display: inline-block;
    text-align: left;
    vertical-align: top;

    &.answer {
      padding: 32px;
      .answer-content {
        width: 100%;
        padding-right: 32px;
        @include font-size(14px);
        color: rgba(0,0,0,0.54);
        margin: 0 auto;
      }
      ul {
        display: table-cell;
        vertical-align: middle;
        width: 50%;
        border-right: 2px solid #E6E6E6;
      }

      li {
        position: relative;
        line-height: 32px;
        cursor: pointer;
        padding: 0 32px;
        text-align: left;

        &:before {
          font-family: arial;
          content: '○';
          position: absolute;
          font-weight: bold;
          font-size: 24px;
          left: 0;
          top: -16px;
          line-height: 63px;
        }

        &.active,
        &:hover {
          color:#13A277;
        }
      }
    }
  }
  .caption-title {
    font-size: 14px;
    font-weight: bold;
    color: rgba(0,0,0,0.87);
  }
  .caption-list-item {
    display: none;
    margin-top: 24px;
    font-size: 14px;
    color: rgba(0,0,0,0.54);
    line-height: 32px;
    &.active {
      display: block;
    }
  }
}

.caption-head {
  font-weight: bold;
}

@import './dist4-media';
